/*HEADER*/
.containerDiv{
    min-height: 100vh;
    position: relative;
    background-size: cover;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
#particles-js{
    width: 100%;
    min-height: 100vh;
    background-color: black;
    //background-size: cover;
    //background-repeat: no-repeat;
    background-position: 50% 50%;
    display: block;
}
canvas{
    background-position: 50% 50%;
    width: 100%;
    min-height: 0vh;
    display: block;
}
.subContainerDiv{
    color: white;
    font-weight: 400;
    font-size: 2rem;
    font-family: 'Open Sans', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top:40%;
    margin: auto;
}
.text-control{
    width: 40%;
    font-size: 1.2rem;
    text-align: justify;
}
.CTAButton{
    margin-top: 20px;
    padding: 15px 60px;
    border: none;
    border-radius: 10px;
    background-color: #386FA4;
    color: white;
    font-family: 'Open Sans', sans-serif;
    text-transform: uppercase;
    font-size: 0.9rem;
    font-weight: 700;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}
.CTAButton:hover{
    cursor: pointer;
}
@media screen and (max-width: 800px){
    .subContainerDiv{
        font-size: 1.6rem;
        text-align: center;
    }
    .text-control{
        width: 70%;
        font-size: 1.2rem;
        text-align: justify;
        margin-top: 35px;
        margin-bottom: 35px;
    }
    canvas{
        position: relative;
    }
}